.submitbtn {
  padding-inline: 1rem;
  padding-block: 0.5rem;
  background-color: #287048;
  border: 0;
  border-radius: 5px;
  color: white;
}
.submitbtn:hover {
  background-color: #256341;
}
.submitbtn:active {
  background-color: #287048;
}
.questioninner {
  display: flex;
  flex-wrap: wrap;
}

.custom-checkbox {
  display: none;
}

/* Custom checkbox styling */
.custom-checkbox-label {
  position: relative;
  padding-left: 30px;
  cursor: pointer;
  display: inline-block;
  line-height: 20px;
}

.custom-checkbox-label input:checked + .custom-checkbox-custom {
  background-color: #287048; /* Change to desired checked color */
  border-color: #282d32; /* Match the border to the background */
}

.custom-checkbox-custom {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: #fff; /* Background color for unchecked state */
  border: 2px solid #2d353e; /* Border color */
  /* transition: background-color 0.3s, border-color 0.3s; */
}
.sucess {
  transition: background-color 5s;

  background-color: #256341;
}
.wrong {
  border-color: rgb(115, 42, 42);
}
.submitbtn:disabled {
  background-color: #2d353e;
}
.round {
  width: 10px;
  height: 10px;
  background-color: #ffffff;
  border-radius: 50%;
}
.noQuestion {
