.QBblock {
  width: 85vw;
  height: 8vh;
  background-color: #0f0f0f;

  border-radius: 5px;
  border: 2px solid hsla(0, 0%, 24%, 0.618);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 1rem;
  gap: 1rem;
}
.QBblock1 {
  width: 65vw;
  height: 8vh;
  background-color: transparent;

  border-radius: 5px;
  /* border: 2px solid hsla(0, 0%, 24%, 0.618); */
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-inline: 1rem;
  gap: 1rem;
}
.QBleft {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.QBright {
  display: flex;
  align-items: center;
  /* gap: rem; */
  justify-content: flex-end;
}
.approveIndicator {
  object-fit: contain;
  height: 1.2rem;
}
.QuestionQB {
  color: white;
}
.QBdate {
  color: white;
}
.QbQuestion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: baseline;
}
.edit {
  object-fit: contain;
  height: 1rem;
}
